.header {
  background-image: url("home_background_phone@3x.41b3495a.jpg");
  background-size: cover;
  padding: 40px 20px 44px;
}

.header__container {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto 54px;
  display: flex;
}

.header__icon-main {
  width: 24px;
  height: 24px;
  fill: none;
  stroke: #fff;
  visibility: visible;
}

.header__logo:hover > svg *, .header__logo:hover .header__main-link, .header__logo:hover {
  stroke: #ff6b08;
  color: #ff6b08;
  cursor: pointer;
  transition: all .4s;
}

.header__links-container {
  gap: 40px;
  display: flex;
}

.header__links {
  color: #fff;
  font-family: Roboto;
  font-size: 12px;
}

.header__links:hover, .header__links:focus {
  color: #ff6b08;
  transition: all .4s;
}

.header__current-link:after {
  content: "";
  width: 34px;
  height: 3px;
  background-color: #ff001b;
  border-radius: 2px;
  display: flex;
}

.header__main-link {
  visibility: hidden;
}

.header__button {
  cursor: pointer;
  background-color: #0000;
  border: none;
  justify-content: flex-end;
  margin-left: 265px;
  display: flex;
  position: absolute;
}

.header__button-icon {
  width: 12px;
  height: 12px;
  fill: none;
  stroke: #fff;
}

.header__button-icon:hover, .header__button-icon:focus {
  stroke: #ff6b08;
  transition: all .4s;
}

.header__form {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.header__input {
  color: #fff;
  width: 280px;
  background-color: #0000;
  border: none;
  border-bottom: 1px solid #fff;
}

.header__input:hover, .header__input:focus {
  color: #ff6b08;
  border-bottom: 1px solid #ff6b08;
  outline: none;
  transition: all .4s;
}

.header__error {
  text-align: center;
  color: #ff001b;
  margin-top: 16px;
  font-family: Roboto;
  font-size: 14px;
}

.header__toggle-container {
  width: 20px;
  height: 20px;
  margin-bottom: 20px;
}

.header__toggle {
  width: 20px;
  height: 20px;
  cursor: pointer;
  background-color: #0000;
  border: none;
  position: relative;
}

.header__toggle__icon {
  fill: #fff;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 0;
  right: 0;
}

@media screen and (min-width: 768px) and (max-width: 1280px) {
  .header .header {
    background-image: url("home_background@3x.73b0196f.jpg");
    background-size: cover;
    padding: 40px 32px 45px;
  }

  .header .header__main-link {
    visibility: visible;
    color: #fff;
    font-family: Roboto;
    font-size: 30px;
    font-weight: 500;
  }

  .header .header__form {
    justify-content: center;
  }

  .header .header__input {
    width: 394px;
  }

  .header .header__button {
    margin-left: 375px;
  }

  .header .header__toggle-container {
    float: right;
  }

  .header .header__error {
    text-align: center;
  }
}

@media screen and (min-width: 1280px) {
  .header .header {
    height: 216px;
    background-image: url("home_background@4x.ae50a285.jpg");
    background-size: cover;
    padding: 40px 32px 45px;
  }

  .header .header__main-link {
    visibility: visible;
    color: #fff;
    font-family: Roboto;
    font-size: 30px;
    font-weight: 500;
  }

  .header .header__form {
    justify-content: center;
  }

  .header .header__input {
    width: 394px;
  }

  .header .header__button {
    margin-left: 375px;
  }

  .header .header__error {
    text-align: center;
    margin-top: 20px;
  }

  .header .header__toggle-container {
    float: right;
