.card {
  font-family: Roboto;
  font-weight: 500;
  transition: transform .3s;
  position: relative;
}

.card:hover, .card:focus {
  transform: scale(1.02);
}

.card__img {
  width: 280px;
  height: 398px;
  border-radius: 5px;
  margin-bottom: 10px;
}

@media screen and (min-width: 768px) {
  .card__img {
    width: 336px;
    height: 455px;
    margin-bottom: 8px;
  }
}

@media screen and (min-width: 1280px) {
  .card__img {
    width: 395px;
    height: 574px;
    margin-bottom: 12px;
  }
}

.card__info {
  font-size: 12px;
  line-height: 1.33;
}

@media screen and (min-width: 1280px) {
  .card__info {
    font-size: 20px;
    line-height: 1.2;
  }
}

.card__title {
  color: #000;
  text-transform: uppercase;
}

.card__title--dark {
  color: #fff;
}

.card__desc {
  color: #ff6b08;
}

.card__link {
  color: #fff;
  cursor: pointer;
  opacity: .9;
  background-image: linear-gradient(45deg, #ff6b08 0%, #f0b719 51%, #fa8130 100%);
  background-size: 200%;
  border: none;
  border-radius: 0 5px 0 10px;
  outline: none;
  padding: 4px 15px;
  font-size: 12px;
  transition: all .5s;
  position: absolute;
  top: 0;
  right: 0;
  box-shadow: 0 0 14px -7px #f0b719;
}

.card__link:hover, .card__link:focus {
  opacity: .9;
  background-position: 100%;
  text-decoration: none;
}

@media screen and (min-width: 1280px) {
  .card__link {
    opacity: .7;
    padding: 2px 20px;
    font-size: 20px;
  }
}

.pagination {
  justify-content: center;
  gap: 4px;
  margin-bottom: 40px;
  display: flex;
}

.pagination--dark {
  color: #fff;
}

@media screen and (min-width: 1280px) {
  .pagination {
    margin-bottom: 60px;
  }
}

.pagination__button {
  width: 40px;
  height: 40px;
  cursor: pointer;
  color: #000;
  background-color: #0000;
  border: none;
  border-radius: 5px;
  transition: background-color .2s linear, color .2s linear;
}

.pagination__button:hover, .pagination__button:focus {
  color: #fff;
  background-color: #ff6b08;
}

.pagination__button--dark {
  color: #fff;
}

.pagination__button--active {
  color: #fff;
  background-color: #ff6b08;
}

.pagination__controls {
  width: 40px;
  height: 40px;
  cursor: pointer;
  color: #000;
  background-color: #f7f7f7;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  transition: background-color .2s linear, color .2s linear;
  display: flex;
}

.pagination__controls:hover, .pagination__controls:focus {
  color: #fff;
  background-color: #ff6b08;
}

.pagination__controls--dark {
  color: #fff;
  background-color: #2e2e2e;
}

.pagination__controls--active {
  color: #fff;
  background-color: #ff6b08;
}

.pagination__numbers {
  gap: 2px;
  display: flex;
}

.pagination__icon {
  width: 16px;
  height: 16px;
  fill: none;
  stroke: #000;
}

.pagination__dots {
  width: 40px;
  height: 40px;
  text-align: center;
  -webkit-user-select: none;
  user-select: none;
  line-height: 40px;
}

.pagination--dark {
  color: #fff;
}

